.Login .loginMain {
  margin: 1.5625rem;
}
.Login .loginHeader {
  margin-bottom: 1.5625rem;
}
.Login .loginHeader .loginTitle {
  font-size: 1.125rem;
  color: #fff;
}
.Login .loginHeader .loginTitleEn {
  font-size: 0.75rem;
  color: #999999;
}
.Login .loginForm .erroy {
  color: red;
  padding-top: 0.46875rem;
  display: flex;
  justify-content: flex-end;
}
.Login .loginForm .loginInput {
  position: relative;
}
.Login .loginForm .loginInput .ant-input-suffix {
  color: #fff;
}
.Login .loginForm .loginInput .ant-form-explain {
  color: red;
}
.Login .loginForm .loginInput input {
  box-sizing: border-box;
  width: 100%;
  height: 2.8125rem;
  border: none;
  color: #666;
  padding-right: 20px;
  background-color: #13141C;
  border-bottom: 1px solid #ccc;
  margin-bottom: 1px;
}
.Login .loginForm .loginInput i {
  position: absolute;
  right: 0px;
  top: 45%;
  margin-bottom: -1.40625rem;
  color: #fff;
}
.Login .loginForm .forgetPassword {
  text-align: center;
  color: #00D2FF;
  margin-top: 0.625rem;
}
.Login .loginForm .customButton {
  margin-top: 2.5rem;
}
.Login .footerRegister {
  position: absolute;
  box-sizing: border-box;
  bottom: 3.125rem;
  width: 100%;
  left: 0px;
  text-align: center;
}
.Login .footerRegister .noRegister {
  color: #fff;
}
.Login .footerRegister .register {
  color: #00D2FF;
}
.loginInput {
  min-height: 68px;
}
.loginInput .hasError {
  color: red;
  margin-top: 0.21875rem !important;
}
.loginInput .am-list-item.am-input-item {
  padding-left: 0px!important;
}
.loginInput .am-list-item .am-list-line {
  padding-right: 0px!important;
}
.loginInput input {
  font-size: 0.625rem !important;
}
.ant-input-affix-wrapper {
  position: relative;
}
.ant-form-explain {
  padding-top: 0.3125rem;
}
html:not([data-scale]) .am-list-body:before {
  height: 0px!important;
}
